.game-area{
  position: relative;
  margin: 50px auto;
  width: 600px;
  height: 600px;
  border: 2px solid #000000;
}

.snake-dot{
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: #00b837;
  z-index: 2;
  border-radius: 30px;
}
.snake-dot:last-child{
  background-color: #005319;
}

.snake-food{
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: #b81010;
  z-index: 1;
  border-radius: 50%;
}
.game-over-overlay{
  width: 600px;
  height: 600px;
  background-color: rgba(0, 0, 0, .7);
  position: relative;
  z-index: 50;
}
.game-over{
  width: 300px;
  height: 300px;
  background-color: #fff;
  position: absolute;
  left: 50%;
  border: 2px solid #000000;
  border-radius: 15px;
  top: 50%;
  margin-left: -150px;
  margin: -150px;
  justify-content: center;
  align-items: center;
  display: flex;
  font-size: 24px;
  flex-direction: column;
  color: #006110;
  text-align: center;
}

.game-over > div {
  margin: 20px;
}
.game-over > div:first-child {
  font-size: 35px;
  color: #b81010;
}
.game-over > div:last-child {
  font-size: 16px;
  color: #1310b8;
}